import React from 'react';
import {Link} from 'react-router-dom';
import style from './Navbar.module.css'

export const Navbar = () => {

    return (
        <div className={style.nav}>
            <Link className={style.active} to="/">  </Link>
            <Link className={style.active} to="/login">  </Link>
            <Link className={style.active} to="/registration">  </Link>
            <Link className={style.active} to="/profile">  </Link>
            <Link className={style.active} to="/404"> 404 </Link>
            <Link className={style.active} to="/password-recovery">   </Link>
            <Link className={style.active} to="/password-recovery/mail@gmail.com">  </Link>
            <Link className={style.active} to="/set-new-password/token">   </Link>
            <Link className={style.active} to="/packs">  </Link>
            <Link className={style.active} to="/cards/:packId">  </Link>
        </div>
    );
};
